<script>
import feather from 'feather-icons';
import { getData, postData } from '@/utils/fetchData';
import axios from 'axios';
export default {
    name: 'CardPanelAdmin',

    created() {
        this.listTrades();
        this.listarAbogados();
    },

    mounted() {
        this.$nextTick(() => {
            feather.replace();
        });
    },

    data() {
        return {
            trades: [],
            abogados: [],
            numbertradesFin: 0,
            numbertradesPend: 0,
            numberTotalResp: 0,
            numbertradesFinResp: 0,
            numbertradesPendResp: 0,
            responsable: ""
        }
    },

    methods: {
        async listTrades() {
            try {
                const result = await getData('trade', this);
                this.trades = result.data;
                const tradesFin = this.trades.filter(trade => trade.tra_state_law === 'F');
                this.numbertradesFin = tradesFin.length;
                const tradesPend = this.trades.filter(trade => trade.tra_state_law === 'P');
                this.numbertradesPend = tradesPend.length;
                //Trades Tardes
                // Obtener la fecha actual
                const currentDate = new Date();
            } catch (error) {
                this.$handleApiError(error, 'listar trades');
            }
        },

        async listarAbogados() {
            try {
                const result = await getData('lawyer', this);
                this.abogados = result.data;
            } catch (error) {
                this.$handleApiError(error, 'listar abogados');
            }finally{
                this.$closeLoadingAlert();
            }
        },

        mostrar(id){
            const abo_id = parseInt(id)
            var tradesTotal = this.trades.filter(trade => trade.lawyer.abo_id === abo_id);
            this.numberTotalResp = tradesTotal.length;
            const tradesFin = tradesTotal.filter(trade => trade.tra_state_law === 'F');
            this.numbertradesFinResp = tradesFin.length;
            const tradesPend = tradesTotal.filter(trade => trade.tra_state_law === 'P');
            this.numbertradesPendResp = tradesPend.length;
        },

        tradeTotal() {
            this.$router.push({ name: 'trade/lista', params: { tipo_tra: '' } });
        },
        tradeTerminado() {
            this.$router.push({ name: 'trade/lista', params: { tipo_tra: 'terminado' } });
        },
        tradePendiente() {
            this.$router.push({ name: 'trade/lista', params: { tipo_tra: 'pendiente' } });
        },
    }


}

</script>

